import {
    HostListener,
    ElementRef,
    Directive,
    Component,
    Renderer,
    OnInit,
    Input,
    AfterContentInit,
    OnChanges,
    ChangeDetectionStrategy
} from '@angular/core';

@Directive({
    selector: 'img[appDragDirective]'
})
export class DragDirective {
    oldLeft: string;
    oldTop: string;

    private isDown = false;
    private disX;
    private disY;
    private disLeft;
    private disTop;

    constructor(
        private elem: ElementRef,
        private render: Renderer
    ) {
        //
    }

    // ç¹å»äºä»¶
    @HostListener('mousedown', ['$event']) onMousedown(event) {
        const wRate = localStorage.getItem('dragWidth');
        const hRate = localStorage.getItem('dragHeight');
        this.isDown = true;

        this.disLeft = this.elem.nativeElement.offsetLeft;
        this.disTop = this.elem.nativeElement.offsetTop;

        this.disX = event.clientX;
        this.disY = event.clientY;
        event.target.style.cursor = 'move';
        // event.preventDefault();
    }

    // çå¬ç§»å¨äºä»¶äºä»¶
    @HostListener('mousemove', ['$event']) onMousemove(event) {
        event.preventDefault();
        // å¤æ­è¯¥åç´ æ¯å¦è¢«ç¹å»äºã

        if (this.isDown) {
            const newdisX = event.clientX - this.disX;
            const newdisY = event.clientY - this.disY;
            this.elem.nativeElement.style.left = newdisX + this.disLeft + 'px';
            this.elem.nativeElement.style.top = newdisY + this.disTop + 'px';

        }
        return false;


    }

    // çå¬documentç¦»å¼äºä»¶

    @HostListener('mouseup', ['$event']) onMouseup() {
        // åªç¨å½åç´ ç§»å¨è¿äºï¼ç¦»å¼å½æ°ä½æä¼è§¦åã
        if (this.isDown) {
            this.isDown = false;
            this.disLeft = this.elem.nativeElement.offsetLeft;
            this.disTop = this.elem.nativeElement.offsetTop;
        }
    }

    // çå¬åç´ ç¦»å¼äºä»¶
    @HostListener('mouseleave', ['$event']) onMouseleave() {
        this.isDown = false;
    }
    ngOnDestroy() {
        //Called once, before the instance is destroyed.
        //Add 'implements OnDestroy' to the class.
        
    }
}
